const ajax = new XMLHttpRequest();
const container = document.getElementById('root');
const content = document.createElement('div');
const NEWS_URL = 'https://api.hnpwa.com/v0/news/1.json';
const CONTENTS_URL = 'https://api.hnpwa.com/v0/item/@id.json';
const store = {
    currentPage : 1,
    feeds:[],
};
// ÌõÑÏóê newsFeed Îøê ÏïÑÎãàÎùº detailÏóêÏÑú Î™©Î°ùÏúºÎ°ú ÎÑòÏñ¥Ïò¨ Í≤ΩÏö∞Ïóê ÌôúÏö©Ìï† Ïàò ÏûàÏúºÎØÄÎ°ú, Ï†ÑÏó≠ Í∞ùÏ≤¥Î°ú ÏÑ§Ï†ïÌïòÏó¨ ÎÇ®Í≤®ÎëîÎã§.

function getData(url){
    ajax.open('GET', url, false);
    ajax.send();

    return JSON.parse(ajax.response);
}

function makeFeeds(feeds){
    for(let i = 0; i < feeds.length; i++){
        feeds[i].read = false;
    }

    return feeds;
}

function newsFeed(){
    let newsFeed = store.feeds;
    const newsList = [];

    if(newsFeed.length === 0){
        newsFeed = store.feeds = makeFeeds(getData(NEWS_URL));
    }

    const maxPage = Math.ceil(newsFeed.length / 10);
    let template = `
    <div class="bg-gray-600 min-h-screen">
        <div class="bg-white text-xl">
        <div class="mx-auto px-4">
            <div class="flex justify-between items-center py-6">
            <div class="flex justify-start">
                <h1 class="font-extrabold">Hacker News</h1>
            </div>
            <div class="items-center justify-end">
                <a href="#/page/{{__prev_page__}}" class="text-gray-500">
                Previous
                </a>
                <a href="#/page/{{__next_page__}}" class="text-gray-500 ml-4">
                Next
                </a>
            </div>
            </div> 
        </div>
        </div>
        <div class="p-4 text-2xl text-gray-700">
        {{__news_feed__}}        
        </div>
    </div>
    `;
    
    for(let i = (store.currentPage - 1) * 10; i < store.currentPage*10; i++)
    {
        newsList.push(`
        <div class="p-6 ${newsFeed[i].read ? 'bg-blue-500' : 'bg-white'} mt-6 rounded-lg shadow-md transition-colors duration-500 hover:bg-green-100">
            <div class="flex">
            <div class="flex-auto">
                <a href="#/show/${newsFeed[i].id}">${newsFeed[i].title}</a>  
            </div>
            <div class="text-center text-sm">
                <div class="w-10 text-white bg-green-300 rounded-lg px-0 py-2">${newsFeed[i].comments_count}</div>
            </div>
            </div>
            <div class="flex mt-3">
            <div class="grid grid-cols-3 text-sm text-gray-500">
                <div><i class="fas fa-user mr-1"></i>${newsFeed[i].user}</div>
                <div><i class="fas fa-heart mr-1"></i>${newsFeed[i].points}</div>
                <div><i class="far fa-clock mr-1"></i>${newsFeed[i].time_ago}</div>
            </div>  
            </div>
        </div>    
      `);   
    }
    
    template = template.replace('{{__news_feed__}}', newsList.join(''));
    template = template.replace('{{__prev_page__}}', store.currentPage > 1 ? store.currentPage - 1 : 1);
    template = template.replace('{{__next_page__}}', store.currentPage === maxPage ? store.currentPage: store.currentPage + 1);

    container.innerHTML = template;
}

function newsDetail(){
    const id = location.hash.substring(7); 
    
    const newsContent = getData(CONTENTS_URL.replace('@id',id));

    let template = `
    <div class="bg-gray-600 min-h-screen pb-8">
      <div class="bg-white text-xl">
        <div class="mx-auto px-4">
          <div class="flex justify-between items-center py-6">
            <div class="flex justify-start">
              <h1 class="font-extrabold">Hacker News</h1>
            </div>
            <div class="items-center justify-end">
              <a href="#/page/${store.currentPage}" class="text-gray-500">
                <i class="fa fa-times"></i>
              </a>
            </div>
          </div>
        </div>
      </div>

      <div class="h-full border rounded-xl bg-white m-6 p-4 ">
        <h2>${newsContent.title}</h2>
        <div class="text-gray-400 h-20">
          ${newsContent.content // Ïª®ÌÖêÏ∏†Í∞Ä ÏïàÎÑòÏò§Ïò¥ APIÎ°ú Ï†úÍ≥µÏù¥ ÏïàÎêòÎäîÎìØÌï®
        } 
        </div>

        {{__comments__}}

      </div>
    </div>
    `;

    for (let i = 0; i < store.feeds.length; i++){
        if(store.feeds[i].id === Number(id)){
            store.feeds[i].read = true;
            break;
        }
    }

    // comment(ÎåìÍ∏Ä)Ïùò Í≤ΩÏö∞ commentÏóê ÌïòÏúÑÏóê Îòê commentÍ∞Ä ÏûàÎäî depth Í∞Ä ÍπäÏñ¥ÏßÄÎäî ÌòïÌÉúÎ°ú ÎêòÏñ¥ÏûàÏñ¥ 
    // Ï¥ù commentÍ∞Ä Î™áÍ∞úÏù∏ÏßÄ ÏïåÍ∏∞ Ïñ¥Î†µÎã§. Í∑∏Îü¨Ìïú Îç∞Ïù¥ÌÑ∞Î•º Ï≤òÎ¶¨ÌïòÎäîÎç∞ ÏûàÏñ¥ Ï±ÑÌÉùÌï† Ïàò ÏûàÎäî Î∞©Î≤ïÏùÑ Í∞ïÍµ¨ÌïúÎã§.
    // commentÏùò uiÎ•º ÎßåÎìúÎäî Í≤ÉÏùÄ Ìï®ÏàòÎ°ú ÎßåÎì§Ïñ¥Ïïº Ìï®ÏùÄ ÏûêÎ™ÖÌïòÎã§. => ÎåìÍ∏ÄÏù¥ Ïó¨Îü¨Í∞úÎã§ ÎùºÍ≥† ÏÉùÍ∞ÅÌïòÎ©¥ Î∞òÎ≥µÌï¥ÏÑú Î∂àÎ¶¨Îäî Íµ¨Ï°∞ÏùºÌÖåÎãà UIÎ•º ÎßåÎì§Í∏∞ÏúÑÌï¥ Ìï®ÏàòÎ°ú Ï†ïÏùòÌïúÎã§.
    function makeComment(comments, called = 0){ // called Ïùò Í∏∞Î≥∏Í∞íÏùÑ 0ÏúºÎ°ú Ìï®ÏúºÎ°úÏç® Ï¥àÍ∏∞Í∞íÏùÑ ÏÑ§Ï†ï.
        const commentString = [];

        for(let i =0; i < comments.length; i++){
            commentString.push(`
            <div style="padding-left: ${called * 40}px;" class="mt-4">
                <div class="text-gray-400">
                    <i class="fa fa-sort-up mr-2"></i>
                    <strong>${comments[i].user}</strong> ${comments[i].time_ago}
                </div>
                <p class="text-gray-700">${comments[i].content}</p>
            </div>   
            `);

            if(comments[i].comments.length > 0){
                commentString.push(makeComment(comments[i].comments, called + 1));
                // Ïû¨Í∑ÄÌò∏Ï∂úÏùÑ ÌÜµÌï¥ Ìï¥Îãπ ÎåìÍ∏ÄÏùò ÎåìÍ∏Ä ÎåÄÎåìÍ∏Ä... ÍπåÏßÄ Î™®Îëê Îã¨ÏïÑÏ§ÄÎã§.
                // Ïù¥Î†áÍ≤å ÎÅùÏùÑ Ïïå Ïàò ÏóÜÎäî Íµ¨Ï°∞Ïóê ÏûêÏ£º ÏÇ¨Ïö©ÎêòÎäî ÌÖåÌÅ¨ÎãâÏù¥Í≥† ÏùµÏàôÌï¥Ï†∏ÏïºÌïúÎã§.
            }
        }

        return commentString.join('');
    }

    container.innerHTML = template.replace('{{__comments__}}', makeComment(newsContent.comments));
}


function router(){
    const routePath = location.hash;
    // location.hashÏóê '#'Îßå Îì§Ïñ¥Í∞ÄÏûàÎäî Í≤ΩÏö∞ÏóêÎäî ÎπàÎ¨∏ÏûêÏó¥ÏùÑ Î∞òÌôòÌïúÎã§.
    if(routePath === ''){
        newsFeed();
    }
    else if(routePath.indexOf('#/page/') >= 0){
        // indexOfÎäî Ïù∏ÏûêÎ°ú Îì§Ïñ¥Ïò® Î¨∏ÏûêÏó¥Ïù¥ Ìè¨Ìï®ÎêòÏñ¥ ÏûàÎã§Î©¥ 0 ~ (Î¨∏ÏûêÏó¥ Í∏∏Ïù¥-1) ÍπåÏßÄÏùò Ïù∏Îç±Ïä§Î•º Î∞òÌôòÌïúÎã§.
        // ÎßåÏùº Ìè¨Ìï®ÎêòÏßÄ ÏïäÏïòÎã§Î©¥ -1ÏùÑ Î∞òÌôòÌïúÎã§.
        store.currentPage = Number(routePath.substring(7));
        newsFeed();
    }
    else{
        newsDetail();
    }
}

window.addEventListener('hashchange', router);
// Í∏∞Ï°¥ hashchangerÏùò Ïù¥Î≤§Ìä∏ Ìï®ÏàòÍ∞Ä newsDetailÎ°ú ÏûëÏÑ±Îêú Í≤ΩÏö∞ Ìï¥ÏãúÍ∞Ä Î∞îÎÄåÎ©¥ Î¨¥Ï°∞Í±¥ Í∏ÄÎÇ¥Ïö©ÏùÑ Î≥¥Ïó¨Ï§ò ÎùºÎäî ÏΩîÎìúÏù¥Îã§.
// Ïù¥Ï†ú hashchangeÏùò Ïù¥Î≤§Ìä∏ Ìï®ÏàòÎ•º routeÎ°ú Ìï¥Ï§åÏúºÎ°úÏç® Í≤ΩÎ°úÏùò Ìï¥ÏãúÍ∞íÏù¥ Î∞îÎÄîÎïåÎßàÎã§ Ïñ¥Îñ§ ÌôîÎ©¥ÏúºÎ°ú Í∞ÄÎäîÏßÄ Ï†ïÌï¥Ï§ÄÎã§.

router();

